.education {
	background: linear-gradient(116.82deg, #b9d4fc 0%, #ecf3fc 100%);
	min-height: 75vh;
	padding-top: 180px;
	padding-bottom: 78px;
	position: relative;
}
.education .title {
	font-size: 72px;
	font-weight: 700;
	text-align: center;
	margin: 0 auto;
	width: 75%;
	line-height: 80px;
}
.education .attend {
	padding: 58px 36px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	cursor: pointer;
}
.education .attend .icons {
	display: flex;
	gap: 24px;
	align-items: center;
}
.education .attend p {
	font-size: 64px;
	font-weight: 700;
	margin: 0;
}
.education .home,
.education .center {
	background-color: rgba(156, 236, 255, 1);
	border-radius: 100px 0 0 100px;
}
.education .mt-72 {
	margin-top: 72px;
}
.education .mt-56 {
	margin-top: 56px;
}
.en .education .online {
	/* .en .education .home { */
	margin-right: 5rem;
}
/* .ar .education .online, */
.ar .education .online {
	margin-left: 5rem;
}
.education .col-md-3 img {
	margin-top: 56px;
}
.education .online {
	background-color: rgba(255, 235, 229, 1);
	border-radius: 0 100px 100px 0;
}
.education .online {
	background-color: rgba(255, 235, 229, 1);
	border-radius: 0 100px 100px 0;
}
.en .education .home,
.en .education .center {
	margin-left: 5rem;
}
.ar .education .center,
.ar .education .home {
	margin-right: 5rem;
}
.education .ring,
.education .microfone {
	position: absolute;
	top: 72px;
}
.education .ring {
	right: 100px;
}
.education .microfone {
	left: 40px;
}
.education .arrow {
	position: absolute;
	left: 33px;
	top: -18px;
}
@media (min-width: 1200px) {
	/* .container,
	.container-lg,
	.container-md,
	.container-sm,
	.container-xl {
		max-width: 1140px;
	} */
}
@media only screen and (max-width: 500px) {
	.education {
		padding-top: 100px;
	}
	.education .mt-72 {
		margin-top: 50px;
	}
	.education .title {
		font-size: 32px;
		width: 90%;
		line-height: 40px;
	}
	.education .ring,
	.entertainment .ring {
		width: 60px;
		right: 20px;
		top: 16px;
	}
	.education .microfone,
	.entertainment .microfone {
		width: 60px;
		left: 20px;
		top: 16px;
	}
	.education .attend {
		padding: 24px 20px;
	}
	.education .attend img {
		width: 32px;
		height: 32px;
	}
	.education .icons img {
		width: 50px;
		height: 50px;
	}
	.education .attend .icons {
		gap: 12px;
	}
	.en .education .home,
	.en .education .center {
		margin-left: 0;
	}
	.ar .education .center,
	.ar .education .home {
		margin-right: 0;
	}
	.education .attend p {
		font-size: 24px;
	}
	.education .edu-img svg {
		width: 32px;
		height: 32px;
		left: 0;
		top: -8px;
	}
	.education .edu-img img {
		width: 100%;
		height: 100%;
	}
	.education .mt-56 {
		margin-top: 30px;
	}
	.en .education .online {
		margin: 0;
	}
}
