.programs .type-tab {
	background: rgba(156, 236, 255, 1) !important;
	border-radius: 10px !important;
	text-align: center;
	padding-top: 44px;
	cursor: pointer;
}
.programs .type-tab .tab-title {
	font-size: 32px;
	font-weight: 700;
	margin-bottom: 65px;
}
.program-type .select-days {
	gap: 70px;
	margin-bottom: 36px;
}
.program-type .select-days span {
	padding: 18px 87px;
	border-radius: 10px;
	border: 1px solid rgba(205, 206, 215, 1);
}
.program-type .program-name {
	font-size: 48px;
	font-weight: 700;
	margin-bottom: 36px;
	line-height: 40px;
}
.program-type .select-days-title {
	font-size: 24px;
	font-weight: 700;
	line-height: 30px;
	margin-bottom: 32px;
}
.program-type .select-days span.active {
	background: rgba(156, 236, 255, 1);
}
.program-type .sub-info {
	background: #fff;
	border-radius: 20px;
	padding: 24px;
	box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
}
.program-type .sub-info .sub {
	border-right: 1px solid gray;
	margin-bottom: 1.5rem;
}
.program-type .sub-info .sub .sub-details {
	color: #000;
	font-size: 18px;
	font-weight: 400;
	margin-bottom: 8px;
}
.program-type .sub-info .sub .sub-title {
	font-size: 24px;
	margin-bottom: 16px;
	font-weight: 700;
	color: #000;
}
