}
.types .container {
	gap: 20px;
	align-items: center;
}
.type-home {
	background-image: url("../../../assets/bg.jpeg");
	background-repeat: round;
	border-radius: 30px;
	padding: 2rem;
	height: 40rem;
	margin: 2rem;
	display: flex;
	align-items: end;
}
.type-home .title {
	font-size: 40px;
	font-weight: bold;
}
.type-home .title span {
	color: var(--orange-color);
}
.type-home .details {
	background: #fff;
	border-radius: 15px;
	padding: 1.5rem;
}
.type-home button {
	float: right;
	background: var(--bg-color);
	padding: 6px 24px;
	border-radius: 10px;
	display: flex;
	gap: 8px;
	font-weight: 500;
}

@media only screen and (max-width: 500px) {
	.types {
		padding: 2rem 0;
	}
	.types .container {
		flex-direction: column;
	}
	.type-home {
		margin: 0;
		padding: 1.5rem;
		height: auto;
	}
	.type-home .title {
		font-size: 18px;
	}
	.header-home .buttons button {
		padding: 12px 24px;
		font-size: 14px;
	}
	.header-home .icons {
		bottom: 20px;
		right: 20px;
	}
	.header-home .icons img,
	.header-home .icons svg {
		width: 44px;
		height: 44px;
	}
}
