.skeleton {
	margin: 10px;
	width: 100%;
	border-radius: 20px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
}
.skeleton.skeleton-loading .image,
.skeleton.skeleton-loading .skeleton-title,
.skeleton.skeleton-loading .skeleton-description {
	background: #eee;
	background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
	border-radius: 10px;
	background-size: 200% 100%;
	animation: 1.5s shine linear infinite;
}

.skeleton.skeleton-loading .image {
	width: 100%;
	height: 300px;
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
}

.skeleton.skeleton-loading .skeleton-title {
	height: 30px;
	width: 100%;
}

.skeleton.skeleton-loading .skeleton-description {
	width: 100%;
	height: 70px;
}

@keyframes shine {
	to {
		background-position-x: -200%;
	}
}
