.courses .course-sec {
	background: rgba(236, 243, 252, 1);
	box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.25);
	border-radius: 30px;
	padding: 40px 32px;
}
.courses .courses-tab {
	display: flex;
	gap: 20px;
	margin-bottom: 32px;
}
.courses .courses-tab span {
	font-size: 24px;
	line-height: 30px;
	font-weight: 700;
	color: #000;
	padding-bottom: 8px;
	cursor: pointer;
}
.courses .courses-tab span.active {
	color: var(--blue-color);
	border-bottom: 1px solid var(--blue-color);
}
.courses .content {
	background: #fff;
	display: flex;
	justify-content: space-between;
	padding: 24px;
	border-radius: 20px;
	border: 1px solid rgba(205, 206, 215, 1);
	margin-bottom: 16px;
}

.courses .content .title,
.courses .content .time {
	display: flex;
	gap: 12px;
	align-items: center;
}
.courses .content .title p {
	font-size: 20px;
	font-weight: 700;
	margin-bottom: 0;
}
.courses .content .time p {
	font-size: 18px;
	font-weight: 400;
	margin-bottom: 0;
}
