.products {
	min-height: 100vh;
	margin-top: 200px;
	margin-bottom: 40px;
	overflow: hidden;
}
.products .category-name {
	font-size: 32px;
	font-weight: 700;
	margin: 0;
}
.products .category-name span {
	font-size: 20px;
	font-weight: 400;
	color: gray;
}
.products button.products-filter {
	background: none;
	border: 1px solid var(--blue-color);
	color: var(--blue-color);
	padding: 12px 100px;
	border-radius: 10px;
	font-weight: 500;
}
.products .products-filter-info {
	background-color: #fff;
	border-radius: 20px;
	padding: 16px 24px;
}
.products .products-filter-info span {
	background-color: var(--blue-bg-color);
	padding: 14px 16px;
	border-radius: 500px;
}
.products .products-filter-info button {
	background: none;
	color: var(--primary-color);
	height: auto;
	font-size: 14px;
	padding: 0;
}
.ar .products .svg {
	transform: rotate(180deg) !important;
}
.filter-btn {
	background-color: var(--blue-color) !important;
	border-color: var(--blue-color) !important;
	color: #fff !important;
	border-radius: 10px;
	width: 100%;
}
.filter-dev .brand-title {
	font-size: 18px;
	margin-bottom: 16px;
	display: block;
	font-weight: 700;
}
.filter-dev .brand-labels {
	gap: 8px;
	display: flex;
	margin-bottom: 12px;
	font-size: 16px;
	font-weight: 400;
	align-items: center;
}
.filter-dev .brand-labels input {
	height: 18px;
	width: 18px;
	outline: none;
	content: none;
}
.products .products-filter {
	background-color: #fff;
	padding: 16px 24px;
	border-radius: 20px;
}
.filter-dev .filter-input {
	width: 100%;
	padding: 9px 12px;
	border: 1px solid rgba(205, 206, 215, 1);
	border-radius: 10px;
}
.modal-content {
	border: 0;
}
.filter-modal .modal-dialog {
	border-radius: 20px !important;
}
.products hr {
	color: #d0d0d0;
}

.type-title span {
	font-size: 18px;
	font-weight: 400;
	line-height: 20.93px;
	color: #848484;
}

.filter-section {
	padding: 0 14px 31px;
	background-color: #f8f8f8;
}
.filter-section h5 {
	margin-left: 75px;
	padding-top: 32px;
	font-size: 24px;
	line-height: 28.85px;
}
.brands .search {
	height: 40px;
	border-radius: 10px;
	border: none;
	width: 100%;
	margin-bottom: 16px;
	padding: 8px;
}
.filter-icon {
	display: none;
}
@media only screen and (max-width: 500px) {
	.products {
		margin-top: 24px;
	}
	.filter-icon {
		display: block;
		width: 30px;
		position: absolute;
		top: 160px;
	}
	.en .filter-icon {
		right: 5px;
	}
	.ar .filter-icon {
		left: 5px;
	}
	.filter-section {
		position: absolute;
		z-index: 2;
		top: 144px;
		height: 100%;
		padding: 0 32px !important;
		display: none;
		transition: all 0.5s ease-in-out;
	}
	.mt-4.col-md-9 {
		margin-top: 0 !important;
	}
	.filter-section.show {
		display: block;
	}
	.filter-section h5 {
		margin-left: 0 !important;
	}
	.brands {
		padding-left: 0 !important;
	}
	.en .products .products {
		padding-right: 0 !important;
	}
	.ar .products .products {
		padding-left: 0 !important;
	}
	.product-card .details h6,
	.product-card .details .price {
		font-size: 16px;
	}
	.products button.products-filter {
		padding: 8px 16px;
		gap: 4px;
	}
	.products svg {
		/* width: 24px;
		height: 24px; */
	}
	.products .category-name {
		font-size: 24px;
	}
}
