.packages-home {
	background: #f0f4f9;
	padding-top: 4rem;
	padding-bottom: 3rem;
	margin-bottom: 4rem;
}
.packages-home .title {
	font-size: 60px;
	margin-bottom: 1rem;
	font-weight: bold;
	text-align: center;
}
.packages-home .description {
	text-align: center;
	width: 75%;
	width: 65%;
	margin: auto;
	margin-bottom: 2rem;
}
.packages-home .expolre-all {
	margin-top: 1rem;
	background: none;
	border-radius: 10px;
	border: 1px solid var(--bg-color);
	color: var(--bg-color);
	font-weight: bold;
	padding: 6px 60px;
}
.packages-home .add-to-cart button {
	background-color: var(--bg-color) !important;
}
@media only screen and (max-width: 500px) {
	.packages-home .title {
		font-size: 30px;
	}
	.packages-home .description {
		width: 85%;
	}
}
