.header-home {
	position: relative;
	height: calc(100vh - 64px);
	overflow: hidden;
	background: #f0f4f9;
	display: flex;
}

.header-home .header-description {
	width: 60%;
}
.header-home .header-title {
	font-size: 60px;
	font-weight: 700;
}
.header-home .getstart {
	background: var(--bg-color);
	font-size: 18px;
	font-weight: bold;
	border-radius: 8px;
	padding: 12px 80px;
	color: #fff;
	text-decoration: none;
}
.carousel-item,
.carousel-inner {
	height: 100%;
}
.header-details {
	position: absolute;
	z-index: 999;
	top: 50%;
	transform: translate(0, -50%);
}
.header-home .bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100% !important;
	height: 100% !important;
}
.header-home .buttons {
	position: absolute;
	top: 50%;
	width: 100%;
	display: flex;
	justify-content: space-around;
}
.header-home .buttons button {
	padding: 12px 80px;
	border-radius: 10px;
	font-size: 16px;
	font-weight: 700;
	display: flex;
	gap: 8px;
	align-items: center;
}
.header-home .buttons .fun {
	background-color: var(--orange-color);
}
.header-home .buttons .learn {
	background-color: var(--blue-color);
}
.ar .header-home .buttons .fun img,
.ar .header-home .buttons .learn img {
	transform: rotate(180deg);
}
@media only screen and (max-width: 500px) {
	.header-home .buttons button {
		padding: 12px 24px;
		font-size: 14px;
	}
	.header-home .header-title {
		text-align: center;
		font-size: 30px;
	}
	.header-home .col-md-7 {
		text-align: center;
	}
	.header-home .header-description {
		width: 90%;
		margin: 20px auto;
		text-align: center;
	}
}
