.en {
	font-family: "HelveticaNeueBlack", sans-serif;
	/* font-family: 'PathwayExtreme', Arial, sans-serif; */
	direction: ltr;
}
.ar {
	direction: rtl;
	/* font-family: "Cairo", sans-serif; */
	font-family: "Almarai", sans-serif;
}

button {
	background-color: black;
	color: white;
	padding: 10px 20px;
	border: none;
	cursor: pointer;
}

/* Header Styles */
header {
	background-color: white;
	padding: 1rem 2rem;
}

header nav ul {
	list-style: none;
	display: flex;
	justify-content: space-around;
	margin: 0;
	padding: 0;
}

header nav ul li {
	margin: 0 1rem;
	color: black;
	cursor: pointer;
}

/* Footer Styles */
.btn-primary {
	background-color: var(--primary-color);
	border-color: var(--primary-color);
}
.btn-primary:hover {
	opacity: 0.8;
	background-color: var(--primary-color);
}
.paths {
	padding: 20px 0;
	margin-bottom: 30px;
}
.paths a {
	background: none !important;
	color: #333 !important;
	padding: 0 !important;
	text-decoration: none;
}
.paths .gt {
	margin: 0 6px;
}
.paths h1 {
	font-size: 24px;
}
.header-home .carousel-indicators button {
	width: 14px;
	height: 14px;
	background-color: #fff !important;
	border-radius: 50%;
}
.header-home .carousel-indicators button.active {
	background-color: #888;
}
.header-home .carousel-control-next,
.header-home .carousel-control-prev {
	display: none;
}

.scroll-animation div p {
	width: 100%;
	overflow: hidden;
}
.scroll-animation:hover div p {
	animation: scroll 5s infinite;
	animation-timing-function: ease-in-out;
}
@keyframes scroll {
	0% {
		translate: 0;
		overflow: visible;
	}
	100% {
		translate: -100%;
		overflow: visible;
	}
}
.error-page {
	margin: 100px 0;
}
.error-page h1 {
	font-size: 72px;
	font-weight: 700;
	margin-bottom: 24px;
}
.error-page h4 {
	font-size: 48px;
	font-weight: 400;
	margin-bottom: 24px;
}
.error-page .btn {
	font-size: 14px;
	width: 400px;
	height: 56px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin: auto;
}
.error-page .btn:hover {
	border-color: var(--primary-color);
}
.no-data {
	text-align: center;
}
.no-data p {
	font-weight: 600;
	font-size: 22px;
}
.section-title {
	margin-bottom: 24px;
	position: relative;
	font-size: 32px;
	font-weight: 700;
	line-height: 43.39px;
}
.gap-4 {
	gap: 4px !important;
}
.gap-8 {
	gap: 8px;
}
.gap-16 {
	gap: 16px;
}
.next-back-buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 32px;
}
.next-back-buttons .next-button {
	background: var(--blue-color);
	color: #fff;
	border: none;
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.next-back-buttons .back-button {
	color: var(--blue-color);
	background: #fff;
	border: 1px solid var(--blue-color);
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
@media only screen and (max-width: 500px) {
}
.gap-32 {
	gap: 32px;
}
