.top-navbar {
	height: 64px;
	padding: 5px 75px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
}
.top-navbar .icons {
	display: flex;
	gap: 8px;
	justify-content: space-between;
}
.top-navbar .icons img {
	cursor: pointer;
}
.top-navbar .language_dropdown {
	/* width: 143px; */
	/* height: 32px; */
	padding: 8px 10px;
	border-radius: 30px;
	background-color: #fff;
}

.top-navbar .language_dropdown a {
	background: none !important;
}
.custom-dropdown ul {
	list-style: none;
}
.navbar .navbar-brand img {
	height: 75px;
	width: 90px;
}
.navbar .one {
	order: 1;
}
.navbar .two {
	order: 3;
	display: flex;
}
.navbar .three {
	order: 2;
}
.navbar .three input {
	padding: 16px;
}
.navbar .navbar-nav {
	gap: 32px;
}
.navbar .three .search {
	width: 400px;
	height: 60px;
	border-radius: 60px;
	border: 1px solid #b9bbc6;
}
.navbar a,
.navbar .nav-link {
	color: #fff;
	font-size: 15px;
	font-weight: 600;
	padding: 6px 8px;
	text-wrap: nowrap;
}
.navbar a:hover {
	color: #dedede;
}
.navbar a.active {
	color: var(--blue-color) !important;
	background-color: #fff;
	border-radius: 30px;
}
.navbar-collapse {
	justify-content: flex-end;
}
.navbar .cart-num {
	background-color: #b96464;
	color: #fff;
	position: absolute;
	border-radius: 50%;
	right: -10px;
	top: 0px;
	padding: 0 7px;
	font-size: 14px;
}
.navbar .categories-menu {
	position: absolute;
	top: 100px;
	left: 1%;
	width: 98%;
	background-color: #fff;
	padding: 1rem;
	/* display: none; */
}
.navbar .category-nav-link:hover .categories-menu {
	display: block;
}
