.menu-lang {
	display: none;
}
@media only screen and (max-width: 500px) {
	.top-navbar .nav-logo {
		height: 30px;
		width: 80px;
	}
	.navbar-nav .nav-link {
		margin-bottom: 1rem !important;
		text-align: center;
	}
	.menu-lang {
		display: block;
	}
	.navbar-toggler {
		border: none;
		box-shadow: none;
	}
	.navbar-toggler {
		z-index: 99;
	}
	.navbar-toggler-icon {
		background-image: url("../../assets/icons/humburger.svg");
	}
	.navbar {
		padding: 0.5rem 8px;
	}
	.lang-translate {
		display: none !important;
	}
	.navbar {
		order: 3;
	}
	.navbar .one {
		order: 1;
	}
	.navbar .two {
		order: 2;
	}
	.navbar .three {
		display: none;
	}
	.en .navbar-collapse {
		right: 0;
	}
	.navbar-collapse {
		position: fixed;
		height: 100vh;
		background: var(--bg-color);
		top: 0;
		z-index: 1;
		padding: 16px 0;
		left: 0;
		width: 100% !important;
		border-bottom: 1px solid #dedede;
	}
	.menu-lang {
		margin-top: 1rem;
		text-align: center;
	}
	.menu-lang span {
		color: #fff !important;
	}
	.navbar .navbar-nav {
		gap: 0;
		padding: 0 12px;
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
		width: 100%;
	}
	.navbar-collapse a {
		margin-bottom: 0;
	}
	.navbar .logo img {
		width: 50px;
		height: 50px;
	}
	.top-navbar {
		padding: 5px 8px;
	}
}
