.entertainment {
	background: linear-gradient(116.82deg, #ffdcd1 0%, #fff6f3 100%);
	min-height: 75vh;
	padding-top: 180px;
	padding-bottom: 78px;
	position: relative;
}
.entertainment .title {
	font-size: 72px;
	font-weight: 700;
	text-align: center;
	margin: 0 auto;
	width: 75%;
	line-height: 80px;
}
.entertainment .attend {
	padding: 0 36px 0 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	cursor: pointer;
	border-radius: 0 100px 100px 0;
}
.entertainment .attend .icons {
	display: flex;
	gap: 24px;
	align-items: center;
}
.entertainment .attend p {
	font-size: 64px;
	font-weight: 700;
	margin: 0;
}
.entertainment .branch-odd {
	background-color: rgba(156, 236, 255, 1);
}
.entertainment .branch-even {
	background-color: rgb(253, 253, 253);
}
.mt-56 {
	margin-top: 56px;
}
.mt-80 {
	margin-top: 80px;
}
.en .entertainment .online {
	/* .en .entertainment .home { */
	margin-right: 5rem;
}
/* .ar .entertainment .online, */
.ar .entertainment .online {
	margin-left: 5rem;
}
.entertainment .col-md-3 img {
	margin-top: 56px;
}
.entertainment .ring,
.entertainment .microfone {
	position: absolute;
	top: 72px;
}
.entertainment .ring {
	right: 100px;
}
.entertainment .microfone {
	left: 40px;
}
.entertainment .arrow {
	position: absolute;
	left: 33px;
	top: -18px;
}
@media only screen and (max-width: 500px) {
	.entertainment .mt-80 {
		margin-top: 40px;
	}
	.entertainment {
		padding-top: 100px;
	}
	.entertainment .title {
		font-size: 32px;
		width: 90%;
		line-height: 40px;
	}
	.entertainment .ring {
		width: 60px;
		right: 20px;
		top: 16px;
	}
	.entertainment .microfone {
		width: 60px;
		left: 20px;
		top: 16px;
	}
	.education .attend {
		padding: 24px 20px;
	}
	.entertainment .attend .icons {
		gap: 12px;
	}
	.entertainment .attend .icons img {
		width: 60px;
		height: 60px;
	}
	.entertainment .attend p {
		font-size: 20px;
	}
	.entertainment .group {
		width: 80px;
		height: 80px;
	}
	.entertainment .arrow1 {
		width: 32px;
		height: 32px;
	}
}
