* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:root {
	--primary-color: #f38b35;
	--bg-primary-color: #f7f9ff;
	--orange-bg-color: #f38b3517;
	--footer-bg: #343434;
	--body-bg: rgba(250, 247, 245, 1);
	--navbar-top-bg: #d0dfff;
	/* --navbar-top-bg: rgba(40, 193, 229); */
	--badge-bg: rgba(99, 142, 200, 1);
	--orange-color: rgba(245, 133, 57, 1);
	--orange-bg-color: rgba(255, 235, 229, 1);
	--blue-color: rgba(15, 80, 166, 1);
	--blue-bg-color: rgba(236, 243, 252, 1);
	--light-blue-color: rgba(101, 159, 234, 1);
	--bg-color: rgba(0, 0, 153, 1);
}
body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: var(--body-bg);
}
.text-primary {
	color: var(--primary-color) !important;
}
.ltr {
	direction: ltr;
}
.rtl {
	direction: rtl;
}
.loader {
	width: 50px;
	margin: auto;
	aspect-ratio: 1;
	display: grid;
	border: 4px solid #0000;
	border-radius: 50%;
	border-right-color: var(--primary-color);
	animation: l15 1s infinite linear;
}
.loader::before,
.loader::after {
	content: "";
	grid-area: 1/1;
	margin: 2px;
	border: inherit;
	border-radius: 50%;
	animation: l15 2s infinite;
}
.loader::after {
	margin: 8px;
	animation-duration: 3s;
}
@keyframes l15 {
	100% {
		transform: rotate(1turn);
	}
}
.opacity-bg {
	background: rgba(30, 31, 36, 0.5);
}
.fun-bg {
	background-color: var(--orange-color);
}
.learn-bg {
	background-color: var(--bg-color);
}
.mix-bg {
	background: linear-gradient(90.13deg, #0f50a6 -8.99%, #f58539 95.94%);
}
.cursor-pointer {
	cursor: pointer;
}
.btn-primary-blue {
	background-color: var(--blue-color);
	border: var(--blue-color);
	color: #fff;
	border-radius: 10px;
	padding: 12px;
	font-size: 16px;
	font-weight: 700;
}
.btn-primary-blue:hover {
	color: #fff;
	background-color: var(--badge-bg);
	border: var(--blue-bg-color);
}
