.product {
	min-height: 100vh;
	margin-top: 200px;
	margin-bottom: 40px;
	overflow: hidden;
}
.product .main-img {
	max-width: 490px !important;
	width: 100% !important;
	min-height: 400px !important;
	max-height: 600px !important;
	height: 100% !important;
	border-radius: 5px;
}
.product .product-name {
	font-size: 32px;
	font-weight: 700;
	line-height: 40px;
	color: #000000;
	margin-bottom: 20px;
}
.product .prices {
	/* width: 50%; */
	display: flex;
	/* justify-content: space-between; */
	align-items: center;
	gap: 24px;
	margin-bottom: 12px;
	height: 35.5px;
}
.product .price {
	font-size: 32px;
	color: var(--primary-color);
	font-weight: 700;
}
.product .description span {
	color: #000;
	font-weight: 500;
	font-size: 20px;
	margin-bottom: 12px;
	display: block;
}

.product .prices p {
	margin: 0;
}
.product .product-category {
	color: var(--primary-color);
	font-size: 20px;
	font-weight: 700;
	line-height: 27.12px;
	text-decoration: underline;
	margin-bottom: 20px;
}
.product .discount {
	font-size: 14px;
	background-color: #fef1e6;
	color: #055902;
	line-height: 19.23px;
	padding: 8px 12px;
	font-weight: 700;
	border-radius: 60px;
}
.product .brand img {
	width: 100px;
	height: 100px;
	border-radius: 4px;
}
.product .brand {
	margin-bottom: 12px;
}
.product .description {
	margin-bottom: 12px;
}
.product .brand span {
	color: #000;
}

.product .swiper-slide img {
	width: 90px !important;
	height: 90px !important;
}
.product .swiper-pagination {
	position: relative;
	margin-top: 1.5rem;
}
.product .amounts {
	border: 1px solid var(--blue-color);
	border-radius: 10px;
	justify-content: space-evenly;
	height: 48px;
}
.product .amounts button {
	font-size: 22px;
	background: none;
	color: var(--primary-color);
	width: 40px;
	border: none;
	line-height: 1;
	cursor: pointer;
}
.product .category {
	font-size: 18px;
	background-color: var(--primary-color);
	width: fit-content;
	color: #fff;
	border-radius: 5px;
	padding: 4px 8px;
}
.product .variant-values {
	gap: 20px 50px;
	display: grid;
	grid-template-columns: auto auto auto;
}
.product .addToCart {
	background-color: var(--primary-color);
	color: #fff;
	text-decoration: none;
	width: 100%;
	font-size: 20px;
	padding: 12px 50px;
	border: 1px solid var(--primary-color);
}
.product .addToCart:hover {
	background: none;
	color: var(--primary-color);
}
.product .amounts span {
	font-size: 22px;
}
.product .old-price {
	text-decoration: line-through;
	font-size: 16px;
	font-weight: 400;
}
.similar_products {
	background-color: #f8f8f8;
	margin-bottom: 64px;
}
.similar_products h1 {
	font-size: 32px;
	font-weight: 700;
	line-height: 37.5px;
	margin-bottom: 32px;
}
@media only screen and (max-width: 850px) {
	.product .main-img {
		width: 280px !important;
		height: 280px !important;
	}
}
@media only screen and (max-width: 500px) {
	.product {
		margin-top: 8rem;
		margin-bottom: 4rem;
		padding: 0 20px;
		min-height: 80vh;
	}
	.product .main-img {
		min-width: 150px !important;
		width: 150px !important;
		min-height: 150px !important;
		height: 150px !important;
		margin-bottom: 1rem;
	}
	.product .swiper-slide img {
		width: 60px !important;
		height: 60px !important;
	}
	.product .col-md-5 {
		margin-bottom: 2rem;
	}
	.similar_products {
		margin: 0;
		padding: 20px;
	}
	.similar_products h1 {
		font-size: 22px;
		margin-bottom: 1.5rem;
	}
	.product .product-name {
		font-size: 22px;
	}
	.product .product-category {
		font-size: 18px;
	}
	.product .price {
		font-size: 20px;
	}
}
