.parent-info {
	min-height: 75vh;
	padding-top: 180px;
	padding-bottom: 27px;
	background: none !important;
}
.parent-info .sub-info {
	background: #fff;
	border-radius: 20px;
	padding: 24px;
	box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.25);
}
.parent-info .upload {
	margin-top: 0;
}
.parent-info .title {
	margin-bottom: 20px;
	font-size: 24px;
	color: #000;
	font-weight: 700;
	margin-top: 16px;
}
.parent-info .form-control,
.parent-info .css-13cymwt-control {
	height: 44px !important;
	border-radius: 10px;
}
.parent-info .add-student {
	margin-top: 20px;
	border-radius: 10px;
	background: var(--blue-color);
	color: #fff;
	font-size: 16px;
	font-weight: 700;
	padding: 10px 26px;
}
.parent-info .add-form label {
	color: #000;
	font-size: 14px;
	font-weight: 700;
	margin-bottom: 0.5rem;
}
.parent-info .gender-input {
	accent-color: #000;
}
.parent-info .parent-info-buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 32px;
}
.parent-info .parent-info-buttons .next-button {
	background: var(--blue-color);
	color: #fff;
	border: none;
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.parent-info .parent-info-buttons .back-button {
	color: var(--blue-color);
	background: #fff;
	border: 1px solid var(--blue-color);
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.congratulations {
	padding-top: 16px;
}
.congratulations img {
	margin-bottom: 24px;
}
.congratulations .congrat-title {
	color: var(--primary-color);
	font-size: 24px;
	font-weight: 700;
	margin-bottom: 16px;
}
.congratulations .message {
	color: gray;
	font-size: 18px;
	font-weight: 400;
	width: 54%;
	text-align: center;
	margin: 0px auto 48px;
}
.skip {
	font-size: 24px;
	margin-bottom: 32px;
	color: rgba(223, 111, 35, 1);
	background: none;
	padding: 0;
	font-weight: 700;
}
