.grade {
	min-height: 75vh;
	padding-top: 180px;
	padding-bottom: 27px;
	background: linear-gradient(116.82deg, #b9d4fc 0%, #ecf3fc 100%);
}
.grade .title {
	margin-bottom: 32px;
	font-size: 32px;
	color: #000;
	font-weight: 700;
}
.grade .types .grade-tab {
	cursor: pointer;
}
.grade .types {
	display: grid;
	grid-template-columns: auto auto auto auto auto auto;
	gap: 24px;
}
.grade .types .grade-tab.active {
	border: 4px solid var(--blue-color);
	border-radius: 32px;
}
.grade .grade-buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 32px;
}
.grade .grade-buttons .next-button {
	background: var(--blue-color);
	color: #fff;
	border: none;
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.grade .grade-buttons .back-button {
	color: var(--blue-color);
	background: #fff;
	border: 1px solid var(--blue-color);
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.grade .kuwaiti-select {
	border-top: 1px solid rgba(205, 206, 215, 1);
	margin-top: 32px;
}
.grade .trac-select {
	color: #000;
	padding: 24px 0;
	font-size: 24px;
	font-weight: 700;
	margin: 0;
}
.grade .science-curriculum {
	display: grid;
	grid-template-columns: auto auto;
	gap: 24px;
}
.grade .science-curriculum div {
	border: 1px solid rgba(205, 206, 215, 1);
	background: #fff;
	border-radius: 10px;
	padding: 49px;
	text-align: center;
	position: relative;
	cursor: pointer;
}
.grade .science-curriculum div input {
	position: absolute;
	top: 18px;
	left: 18px;
	accent-color: #000;
	width: 26px;
	height: 26px;
}
@media only screen and (max-width: 500px) {
	.grade {
		padding-top: 100px;
	}
	.grade .types {
		gap: 12px;
		grid-template-columns: auto auto auto auto;
	}
	.grade .grade-tab img {
		width: 100%;
	}
	.grade .types .grade-tab.active {
		border-radius: 15px;
	}
}
