.education-type {
	min-height: 75vh;
	padding-top: 100px;
	padding-bottom: 27px;
	background: linear-gradient(116.82deg, #b9d4fc 0%, #ecf3fc 100%);
}
.education-type .title {
	margin-bottom: 32px;
	font-size: 32px;
	color: #000;
	font-weight: 700;
}
.education-type .types {
	display: grid;
	grid-template-columns: auto auto auto;
	gap: 24px;
}
.education-type .types .type-tab {
	background: linear-gradient(
		134.9deg,
		rgba(255, 202, 186, 0.5) 31.26%,
		rgba(103, 186, 235, 0.5) 94.25%
	);
	border-radius: 30px;
	text-align: center;
	cursor: pointer;
	padding-bottom: 16px;
}
.education-type .types .type-tab.active {
	border: 4px solid var(--blue-color);
}
.education-type .types .type-tab .tab-title {
	background: radial-gradient(100% 1350.56% at 0% 0%, #0a4ea2 0%, #f24e29 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	-webkit-text-strokewidth: 1px;
	border-image-source: linear-gradient(180deg, #0a4ea2 0%, #041d3c 100%);
	font-size: 48px;
	font-weight: 700;
	text-transform: capitalize;
	margin-bottom: 49px;
	margin-top: 44px;
}
.education-type .next-back-buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 32px;
}
.education-type .next-back-buttons .next-button {
	background: var(--blue-color);
	color: #fff;
	border: none;
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.education-type .next-back-buttons .back-button {
	color: var(--blue-color);
	background: #fff;
	border: 1px solid var(--blue-color);
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.education-type .kuwaiti-select {
	border-top: 1px solid rgba(205, 206, 215, 1);
	margin-top: 32px;
}
.education-type .trac-select {
	color: #000;
	padding: 24px 0;
	font-size: 24px;
	font-weight: 700;
	margin: 0;
}
.education-type .foundation-curriculum {
	display: grid;
	grid-template-columns: auto auto;
	gap: 24px;
}
.education-type .foundation-curriculum div {
	border: 1px solid rgba(205, 206, 215, 1);
	background: #fff;
	border-radius: 10px;
	padding: 49px;
	text-align: center;
	position: relative;
	cursor: pointer;
}
.education-type .foundation-curriculum div input {
	position: absolute;
	top: 18px;
	left: 18px;
	accent-color: #000;
	width: 26px;
	height: 26px;
}
.education-type .start-date {
	border: 1px solid rgba(205, 206, 215, 1);
	padding: 10px 12px;
	font-size: 12px;
	border-radius: 10px;
	width: 50%;
}
.fs-16 {
	font-size: 16px;
}
.booking-modal .react-calendar {
	width: 100%;
	border: none;
}
.booking-modal .react-calendar button {
	color: #000;
	border-radius: 4px;
}
/* .booking-modal .react-calendar__tile--now:enabled:hover {
	background-color: #fff;
} */
.booking-modal .react-calendar__tile:disabled {
	background-color: #fff;
	color: #c8c6c6 !important;
	cursor: not-allowed;
}

.booking-modal .react-calendar__tile--active {
	color: #fff !important;
}
.booking-modal .timeslot {
	display: grid;
	grid-template-columns: auto auto;
	gap: 16px 24px;
}
.booking-modal .timeslot span {
	text-align: center;
	padding: 12px;
	line-height: 20px;
	font-size: 16px;
	cursor: pointer;
}
.booking-modal .timeslot span.active {
	background-color: rgba(255, 202, 186, 1);
	border-radius: 10px;
}
.success-modal .success-title {
	font-size: 16px;
	margin-top: 8px;
	color: rgba(0, 163, 46, 1);
	font-weight: 700;
	margin-bottom: 18px;
}
.success-modal .success-message {
	font-weight: 400;
	color: rgba(98, 99, 108, 1);
}
@media only screen and (max-width: 500px) {
	.education-type {
		padding-top: 100px;
	}
	.education-type .title {
		font-size: 24px;
	}
	.education-type .types {
		background: none;
		grid-template-columns: auto auto !important;
	}
	.education-type .types .type-tab .tab-title {
		font-size: 32px;
		margin: 24px 0;
	}
	.education-type .types .type-tab img {
		width: 75%;
	}
}
