.age-type {
	min-height: 75vh;
	padding-top: 180px;
	padding-bottom: 27px;
}
.age-type .title {
	margin-bottom: 32px;
	font-size: 32px;
	color: #000;
	font-weight: 700;
}
.age-type .types {
	display: grid;
	grid-template-columns: auto auto auto;
	gap: 24px;
	background: none;
}
.age-type .types .type-tab {
	background: none;
	cursor: pointer;
}
.age-type .types .type-tab.active {
	border: 4px solid var(--blue-color);
	border-radius: 30px;
}
.gender-select {
	border-top: 1px solid rgba(205, 206, 215, 1);
	padding-top: 24px;
	margin-top: 32px;
}
.gender-all > div > div {
	border: 1px solid rgba(205, 206, 215, 1);
	background: #fff;
	border-radius: 10px;
	padding: 49px;
	text-align: center;
	position: relative;
	cursor: pointer;
}
.gender-all div input {
	position: absolute;
	top: 18px;
	left: 18px;
	accent-color: #000;
	width: 26px;
	height: 26px;
}
.gender-select .trac-select {
	font-size: 24px;
	color: #000;
	font-weight: 700;
	margin-bottom: 24px;
}
@media only screen and (max-width: 500px) {
	.age-type {
		padding-top: 80px;
	}
	.age-type .title {
		margin-bottom: 24px;
		font-size: 24px;
	}
	.age-type .types {
		gap: 18px;
	}
	.age-type .types .type-tab img {
		width: 100%;
	}
	.age-type .types .type-tab.active {
		overflow: hidden;
		border-radius: 15px;
	}
	.gender-all div input {
		top: 12px;
		left: 12px;
		width: 22px;
		height: 22px;
	}
	.gender-all > div > div img {
		width: 50px;
		height: 50px;
	}
	.gender-all > div > div {
		padding: 20px;
	}
}
