.subjects-sec {
	min-height: 75vh;
	padding-top: 180px;
	padding-bottom: 27px;
	background: linear-gradient(116.82deg, #b9d4fc 0%, #ecf3fc 100%);
}
.subjects-sec .title {
	margin-bottom: 32px;
	font-size: 32px;
	color: #000;
	font-weight: 700;
}
.multi-chooes {
	font-size: 24px;
	font-weight: 400;
	color: gray;
}
.subjects-sec .types .sub-tab {
	cursor: pointer;
	background: #fff;
	text-align: center;
	padding: 26px 0;
	border-radius: 10px;
}
.subjects-sec .types .sub-tab .sub-title {
	font-size: 32px;
	font-weight: 700;
	padding-top: 8px;
}
.subjects-sec .types {
	display: grid;
	grid-template-columns: auto auto auto auto auto auto;
	gap: 24px;
}
.subjects-sec .types .sub-tab.active {
	border: 4px solid var(--blue-color);
	border-radius: 10px;
}
.subjects-sec .subjects-buttons {
	display: flex;
	justify-content: space-between;
	margin-top: 32px;
}
.subjects-sec .subjects-buttons .next-button {
	background: var(--blue-color);
	color: #fff;
	border: none;
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.subjects-sec .subjects-buttons .back-button {
	color: var(--blue-color);
	background: #fff;
	border: 1px solid var(--blue-color);
	border-radius: 10px;
	height: 44px;
	font-weight: 700;
	display: flex;
	gap: 8px;
}
.subjects-sec .kuwaiti-select {
	border-top: 1px solid rgba(205, 206, 215, 1);
	margin-top: 32px;
}
.subjects-sec .kuwaiti-select .sub-info {
	background: #fff;
	border-radius: 20px;
	padding: 24px;
	box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
}
.subjects-sec .kuwaiti-select .sub-info .sub {
	border-right: 1px solid gray;
	margin-bottom: 1.5rem;
}
.subjects-sec .kuwaiti-select .sub-info .sub .sub-details {
	color: #000;
	font-size: 18px;
	font-weight: 400;
	margin-bottom: 8px;
}
.text-blue {
	color: var(--blue-color);
}
.subjects-sec .kuwaiti-select .sub-info .sub .sub-title {
	font-size: 24px;
	margin-bottom: 16px;
	font-weight: 700;
	color: #000;
}
.subjects-sec .no-classes-sub {
	display: flex;
	align-items: center;
	gap: 40px;
	margin-bottom: 22px;
}
.subjects-sec .add-form label {
	font-size: 14px;
	color: #000;
	font-weight: 700;
}
.subjects-sec .trac-select {
	color: #000;
	padding: 24px 0;
	font-size: 24px;
	font-weight: 700;
	margin: 0;
}
.fs-20 {
	font-size: 20px;
}
.fs-22 {
	font-size: 22px;
}
.fs-24 {
	font-size: 24px;
}
.add-one .details h6,
.add-one .details .price {
	font-size: 16px;
	line-height: 20px;
}
.add-one .details .prices {
	height: auto;
}
.add-one .add-one-title {
	margin-bottom: 24px;
	margin-top: 32px;
}
.add-one .add-one-total {
	background: #fff;
	padding: 26px 75px;
	border-radius: 20px;
	line-height: 29.3px;
	margin-top: 17px;
}
@media only screen and (max-width: 500px) {
	.subjects-sec {
		padding-top: 100px;
	}
	.subjects-sec .title {
		font-size: 24px;
	}
	.multi-chooes {
		font-size: 18px;
	}
}
