@media only screen and (max-width: 500px) {
	.contact {
		padding: 1rem;
		margin-top: 2rem;
	}
	.contact .row div:nth-of-type(3) {
		margin-top: 2rem;
	}
	.login-form,
	.signup-form {
		padding: 1rem 0;
	}
	.contact .contact-title {
		font-size: 32px;
	}
	.contact .contact-subtitle {
		font-size: 18px;
	}
	.contact .contact-social .social-title,
	.contact .contact-social img,
	.contact .contact-social .social-subtitle {
		margin-bottom: 8px;
	}
	.contact .contact-social {
		margin-bottom: 32px;
	}
	.contact .row div:nth-of-type(3) {
		margin-top: 0;
	}
	.contact .join-us-form {
		padding: 16px;
	}
	.contact label {
		font-weight: 500;
	}
	.contact .suggestion-form .contact-tabs .tab {
		font-size: 18px;
		padding-bottom: 4px;
	}
	.contact .suggestion-form {
		padding: 32px 20px;
	}
	.contact .apply {
		font-weight: 600;
	}
}
