.table-info {
	min-height: 75vh;
	padding-top: 180px;
	padding-bottom: 27px;
	background: linear-gradient(116.82deg, #b9d4fc 0%, #ecf3fc 100%);
}
.table-info .table-sec {
	background: #fff;
	border-radius: 20px;
	padding: 24px;
	width: 85%;
	margin: auto;
	box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.25);
}
.table-info .table-head {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 32px;
}
.table-info .title {
	font-size: 24px;
	color: #000;
	font-weight: 700;
	margin-bottom: 0;
}
.table-info .table-head div {
	display: flex;
	gap: 16px;
}
.table-info .table-head input {
	border-radius: 10px;
	border: 1px solid rgba(205, 206, 215, 1);
	padding: 10px 100px 10px 16px;
	font-weight: 500;
}
.table-info .table-head button {
	border: 1px solid var(--blue-color);
	color: var(--blue-color);
	font-size: 16px;
	border-radius: 10px;
	background: none;
	display: flex;
	font-weight: 700;
	gap: 8px;
	padding: 12px 24px;
	line-height: 20px;
}
.table-info .table-div p {
	font-size: 18px;
	margin-bottom: 24px;
	font-weight: 700;
}
.table-info .table-div .table {
	margin: 0;
}
.table-info .table-div .table-responsive {
	padding: 19px 12px;
	border: 1px solid rgba(205, 206, 215, 1) !important;
}
.table-info .table-div .table-responsive th,
.table-info .table-div .table-responsive td {
	padding: 18px 40px;
	border: 6px solid #fff;
	border-radius: 12px;
}

.table-info .table-div .table-responsive td {
	background: var(--blue-bg-color);
	font-size: 20px;
	font-weight: 400;
}
.table-info .table-div .table-responsive .day {
	background: rgba(250, 247, 245, 1);
	color: rgba(245, 133, 57, 1);
}
.fw-700 {
	font-weight: 700 !important;
}
.table-info .table-div .table-responsive .parent {
	background: var(--light-blue-color);
	color: #fff;
	font-size: 24px;
	font-weight: 700;
}
.table-info .table-div .table-responsive .sub {
	background: rgba(15, 130, 166, 1);
	color: #fff;
	font-size: 24px;
	font-weight: 700;
}
.table-info .any-prob {
	background: rgba(255, 235, 229, 1);
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 24px;
	border-radius: 10px;
	font-weight: 700;
}
.table-info .any-prob .contact-btn {
	border: 1px solid var(--blue-color);
	color: var(--blue-color);
	display: flex;
	background: #fff;
	gap: 8px;
	align-items: center;
	border-radius: 10px;
	padding: 12px 30px;
	font-weight: 700;
}
.table-info .table-filter {
	background: rgba(250, 247, 245, 1);
	border-radius: 20px;
	padding: 16px 24px;
}
.table-info .table-filter span {
	background-color: var(--blue-bg-color);
	padding: 14px 16px;
	border-radius: 500px;
	font-weight: 700;
	font-size: 14px;
}
.table-info .table-filter img {
	cursor: pointer;
}
.table-info .table-filter button {
	background: none;
	color: var(--primary-color);
	height: auto;
	font-size: 14px;
	padding: 0;
	font-weight: 700;
}
