.terms-modal .terms-title {
	font-size: 24px;
	font-weight: 700;
	margin-bottom: 24px;
}
.filter-modal label,
.terms-modal label {
	font-size: 14px;
	font-weight: 700;
	margin-bottom: 28px;
}
.filter-modal input {
	accent-color: #000;
}
.filter-modal {
	font-weight: 500;
}
.filter-modal label {
	margin-bottom: 0.5rem;
}
.terms-modal label input {
	accent-color: #000;
	width: 22px;
	height: 22px;
}
.filter-modal .filter-btn {
	font-weight: 700;
}
.terms-modal button {
	width: 100%;
	background-color: var(--blue-color);
	color: #fff;
	border-radius: 10px;
	border: none;
	height: 40;
	font-weight: 700;
}
.terms-modal button:disabled {
	opacity: 0.7;
	cursor: not-allowed;
}
