.programs-type {
	min-height: 75vh;
	padding-top: 140px;
	padding-bottom: 27px;
}
.programs-type .title {
	margin-bottom: 32px;
	font-size: 32px;
	color: #000;
	font-weight: 700;
}
.programs .type-tab {
	background: rgba(255, 235, 229, 1) !important;
	border-radius: 10px !important;
	text-align: center;
	padding-top: 44px;
	cursor: pointer;
}
.programs .type-tab .tab-title {
	font-size: 32px;
	font-weight: 700;
	margin-bottom: 65px;
}
.entertainmentprogram .select-days {
	gap: 70px;
	margin-bottom: 36px;
}
.entertainmentprogram .select-days span {
	padding: 18px 87px;
	border-radius: 10px;
	font-weight: 500;
	border: 1px solid rgba(205, 206, 215, 1);
}
.entertainmentprogram .program-name {
	font-size: 48px;
	font-weight: 700;
	margin-bottom: 36px;
	line-height: 40px;
}
.entertainmentprogram .select-days-title {
	font-size: 24px;
	font-weight: 700;
	line-height: 30px;
	margin-bottom: 32px;
}
.entertainmentprogram .select-days span.active {
	background: rgba(255, 202, 186, 1);
}
.entertainmentprogram .sub-info {
	padding: 24px;
}
.entertainmentprogram .sub-info .actions {
	border: 1px solid rgba(15, 80, 166, 1);
	border-radius: 10px;
	padding: 4px;
}
.entertainmentprogram .sub-info .sub {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 1rem;
	border-bottom: 1px solid #dedede;
	padding-bottom: 12px;
}
.entertainmentprogram .sub-info .sub:last-of-type {
	border: 0;
}
.entertainmentprogram .sub-info .sub .sub-details {
	color: #000;
	font-size: 18px;
	font-weight: 400;
	margin-bottom: 8px;
}
.entertainmentprogram .sub-info .sub .sub-title {
	font-size: 24px;
	margin-bottom: 16px;
	font-weight: 700;
	color: #000;
}
.entertainmentprogram .more-info {
	background-color: #fff;
	border-radius: 20px;
	padding: 24px;
	box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.15);
}
